"use client";

import { useState } from "react";
import AuthForm from "@/app/components/AuthForm";
import InputField from "@/app/components/InputField";
import Button from "@/app/components/Button";

export default function LoginPage() {
  const [form, setForm] = useState({ email: "", password: "" });
  const [error, setError] = useState({});

  const handleSubmit = () => {
    let err = {};
    if (!form.email) err.email = "Email required";
    if (!form.password) err.password = "Password required";

    setError(err);
    if (Object.keys(err).length === 0) {
      console.log("Login Success:", form);
    }
  };

  return (
    <AuthForm
      title="Login"
      footer={<a href="/auth/forgot" className="text-blue-600">Forgot Password?</a>}
    >
      <InputField label="Email" type="email" value={form.email}
        onChange={e => setForm({ ...form, email: e.target.value })}
        error={error.email}
      />
      <InputField label="Password" type="password" value={form.password}
        onChange={e => setForm({ ...form, password: e.target.value })}
        error={error.password}
      />
      <Button onClick={handleSubmit}>Login</Button>
    </AuthForm>
  );
}
